export default {
	data(){
		return{
			specClass: 'none',
			scrollTop:0,
			navnarwidth:0,
			windowHeight:0,
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500
		}
	},
	onShow() {
		var _that = this
		wx.getSystemInfo({
		  success: function(res) {
			  console.log(res.statusBarHeight)
			_that.navnarHeight = res.statusBarHeight
			_that.windowHeight = res.windowHeight
		  }
		})
	},
	methods:{
		upper(){
			
		},
		lower(){
		},
		scroll(){
		
		},
		
		
		//规格弹窗开关
		toggleSpec() {
			if(this.specClass === 'show'){
				this.specClass = 'hide';
				setTimeout(() => {
					this.specClass = 'none';
				}, 250);
			}else if(this.specClass === 'none'){
				this.specClass = 'show';
			}
		},
		
		//展示图片
		ShowImage(){
			let src = 'https://ae01.alicdn.com/kf/HTB1wREwTXzqK1RjSZFvq6AB7VXaT.jpg';
			let srclist = [
				'https://ae01.alicdn.com/kf/HTB1wREwTXzqK1RjSZFvq6AB7VXaT.jpg',
				'https://ae01.alicdn.com/kf/HTB1wREwTXzqK1RjSZFvq6AB7VXaT.jpg',
				'https://ae01.alicdn.com/kf/HTB1wREwTXzqK1RjSZFvq6AB7VXaT.jpg'
			]
			uni.previewImage({
				current:src,
				urls: srclist
			});
		},
		
		stopPrevent(){}
	}
}
